import React, { Component } from 'react';

class Name extends Component {

  render() {
    const { contact } = this.props
    return (
      <section className="hdr">
        <div className="hdr-name">
          <h1>{contact.name}</h1>
        </div>
        <div className="hdr-address">
          {contact.address1}<br/>
          {contact.address2}<br/>
          {contact.tel}<br/>
          {contact.email}
        </div>
      </section>
    );
  }
}

export default Name



