import React, { Component } from 'react';
import { connect } from 'react-redux';

import Name from './Name';
import TextSection from './TextSection';
import Section from './Section';

class CV extends Component {
  render() {
    const { cv } = this.props
    return this.props.fileLoaded && (
      <div>
        <Name contact={cv.contact}/>
        <TextSection title='Personal Profile' text={cv.profile} />
        <Section title='Employment History' positions={cv.positions} />
        <Section title='Education' positions={cv.education} />
        <Section title='Training and Education' positions={cv.training} />
        <Section title='Other Information' positions={cv.other} />
        <TextSection title='References' text={cv.references} />
      </div>
    )
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    fileLoaded: state.fileLoaded,
    cv: state.cv
  }
}

export default connect(mapStateToProps)(CV);



