import React, { Component } from 'react';

class WorkPosition extends Component {

  render() {
    const { position } = this.props
    return (
      <article>
        <div className="art-meta">
          <div className="art-meta-title">{position.title}</div>
          <div className="art-meta-dates">{position.dates}</div>
          <div className="art-meta-company">{position.company}</div>
          <div className="art-meta-address">{position.companyAddress}</div>
        </div>
        <div className="art-body">
          <p>{position.body}</p>
          <dl>
            { position.keyRoles && 
              <dt>Key roles</dt> }
            { position.keyRoles && 
              <dd>{position.keyRoles}</dd> }

            { position.responsibilities && 
              <dt>Responsibilities</dt>} 
            { position.responsibilities &&  
              <dd>{position.responsibilities}</dd> }

            { position.skillsDeveloped && 
              <dt>Skills developed</dt> }
            { position.skillsDeveloped && 
              <dd>{position.skillsDeveloped}</dd> }
          </dl>
        </div>
      </article>
    );
  }
}

export default WorkPosition



