function reducer(state = initialState, action) {
  const partialState = {}
  switch (action.type) {
    case UPLOAD_CV:
      partialState.cv = action.payload.cv
      partialState.filename = action.payload.filename
      partialState.fileLoaded = true
      break
    default: break
  }

  return { ...state, ...partialState };
}

export default createStore(reducer, applyMiddleware(logger))



