import React, { Component } from 'react';
import WorkPosition from './WorkPosition';

class Work extends Component {

  render() {
    return (
      <section>
        <div className="sct-hdr">
          <div className="sct-hdr-line">&nbsp;</div>
          <h3>{this.props.title}</h3>
        </div>
        { this.props.positions.map((p, i) => (
          <WorkPosition key={i} position={p} />
        )) }
      </section>
    );
  }
}

export default Work



