import React, { Component } from 'react';

class Profile extends Component {

  render() {
    return (
      <section>
        <div className="sct-hdr">
          <div className="sct-hdr-line">&nbsp;</div>
          <h3>{this.props.title}</h3>
        </div>
        <p>{this.props.text}</p>
      </section>
    );
  }
}

export default Profile



