import React, { Component } from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import { uploadCV } from '../store';
import './Header.css'

class Header extends Component {

  readFile(evt) {
    var files = evt.target.files
    var file = files[0]  
    var reader = new FileReader()
    const { upload } = this.props
    reader.onload = function(event) {
      upload(JSON.parse(event.target.result), file.name)        
    }
    reader.readAsText(file)
  }

  download() {
    var element = document.createElement('a')
    element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(JSON.stringify(this.props.cv)))
    element.setAttribute('download', this.props.filename)

    element.style.display = 'none'
    document.body.appendChild(element)

    element.click()

    document.body.removeChild(element)
  }

  render() {
    return (
      <header className="no-print">
        <input 
          type="file" 
          name="file" 
          encType="multipart/form-data" 
          onChange={(ev) => {this.readFile(ev)}}
        />
        <button onClick={() => { this.download() }}>Download</button>
      </header>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    cv: state.cv,
    filename: state.filename
  }
}

const mapDispatchToProps = (dispatch) => bindActionCreators(
  {
    upload: uploadCV
  },
  dispatch
)

export default connect(mapStateToProps, mapDispatchToProps)(Header)



